@import "~modern-normalize/modern-normalize.css";
@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,600;1,400&family=Open+Sans:ital,wght@0,400;0,600;1,400&family=Roboto+Mono:ital,wght@0,400;0,600;1,400&display=swap");

:root {
  --color: #333;
  --bg-color: #fff;
}

.dark-theme {
  --color: #c5c8c6;
  --bg-color: #282a2e;
}

body {
  color: var(--color);
  background-color: var(--bg-color);
  font-family: "Open Sans", sans-serif;
  font-size: 11pt;
  font-weight: 400;
  line-height: 1.5;
  max-width: 960px;
  margin: 0px auto;
  transition-property: color, background-color;
  transition-duration: 200ms;
  transition-timing-function: ease-in-out;
}

html[lang="en"],
html[lang="de"] {
  body.serif {
    font-family: "Lora", serif;
  }

  body.monospace {
    font-family: "Roboto Mono", monospace;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 18pt;
  text-transform: uppercase;
}

h2 {
  font-size: 10pt;
  letter-spacing: 2pt;
  font-weight: 600;
  text-transform: uppercase;
}

strong {
  font-weight: 600;
}

small {
  font-size: 10pt;
}

button {
  display: block;
  color: var(--color);
  cursor: pointer;
  appearance: none;
  background-color: transparent;
  border-radius: 0;
  border: none;
  margin: 0;
  padding: 0;
}

.polka {
  width: 320pt;
  height: 220pt;
  position: absolute;
  top: 10pt;
  left: 0;
  opacity: 0.3;
  background-image: radial-gradient(var(--color) 1px, transparent 1px);
  background-size: 10px 10px;
}

@page {
  size: auto;
  margin: 20pt;
}

@media print {
  body {
    color: #000;
  }

  .hide-print {
    display: none;
  }
}

@import "spacing";
@import "text";
@import "flex";
